/* scrollToTop button refer to https://codepen.io/matthewcain/pen/ZepbeR */

.scrollToTopBtn {
	display: inline-block;
	background-color: #FF9800;
	width: 50px;
	height: 50px;
	text-align: center;
	border-radius: 4px;
	position: fixed;
	bottom: 30px;
	right: 30px;
	transition: background-color .3s, 
	  opacity .5s, visibility .5s;
	opacity: 0;
	visibility: hidden;
	z-index: 10000;
  }
  .scrollToTopBtn::after {
	content: "T";
	font-weight: normal;
	font-style: normal;
	font-size: 2em;
	line-height: 50px;
	color: #fff;
  }
  .scrollToTopBtn:hover {
	cursor: pointer;
	background-color: #333;
  }
  .scrollToTopBtn:active {
	background-color: #555;
  }
  .scrollToTopBtn.show {
	opacity: 1;
	visibility: visible;
  }

