/* I refer to Paul's cool animation design on codepen and fix a few display width bugs: https://codepen.io/paulvddool/pen/yYeeVE */
#animation {
	display: block;
	margin: 20% auto;
	height: 80px;
	width: auto;
}
@media screen and (max-width: 1080px) {
	#animation {
		box-sizing: border-box;
		-moz-box-sizing: border-box;
		padding: 0px;
		width: 100%;
	}
}
@keyframes animateOnce {
	0% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform: translateY(80px); }
	100% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform: translateY(0px); }
}
@-webkit-keyframes animateOnce {
	0% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform: translateY(80px); }
	100% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform: translateY(0px); }
}
#text {
	width: 350px;
	height: 80px;
	float: left;
	overflow: hidden;
}
#text h1 {
	animation: animateOnce 0.2s;
	-webkit-animation: animateOnce 0.2s;
}
@keyframes animateFirst {
	0% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform:translateY(80px); }
	1% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform:translateY(0px); }
	14% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform:translateY(0px); }
	15% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform:translateY(-80px); }
	70% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform:translateY(-80px); }
	71% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform:translateY(-160px); }
	99% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform:translateY(-160px); }
	100% { transform: translateY(-240px); -webkit-transform: translateY(-240px); -moz-transform:translateY(-240px); }
}
@-webkit-keyframes animateFirst {
	0% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform:translateY(80px); }
	1% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform:translateY(0px); }
	14% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform:translateY(0px); }
	15% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform:translateY(-80px); }
	70% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform:translateY(-80px); }
	71% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform:translateY(-160px); }
	99% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform:translateY(-160px); }
	100% { transform: translateY(-240px); -webkit-transform: translateY(-240px); -moz-transform:translateY(-240px); }
}
@keyframes animateSecond {
	0% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform: translateY(80px); }
	14% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform: translateY(80px); }
	15% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform: translateY(0px); }
	29% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform: translateY(0px); }
	30% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform: translateY(-80px); }
	44% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform: translateY(-80px); }
	45% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform: translateY(-160px); }
	59% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform: translateY(-160px); }
	60% { transform: translateY(-240px); -webkit-transform: translateY(-240px); -moz-transform: translateY(-240px); }
	70% { transform: translateY(-240px); -webkit-transform: translateY(-240px); -moz-transform: translateY(-240px); }
	71% { transform: translateY(-320px); -webkit-transform: translateY(-320px); -moz-transform: translateY(-320px); }
	85% { transform: translateY(-320px); -webkit-transform: translateY(-320px); -moz-transform: translateY(-320px); }
	86% { transform: translateY(-400px); -webkit-transform: translateY(-400px); -moz-transform: translateY(-400px); }
	99% { transform: translateY(-400px); -webkit-transform: translateY(-400px); -moz-transform: translateY(-400px); }
	100% { transform: translateY(-480px); -webkit-transform: translateY(-480px); -moz-transform: translateY(-480px); }
}
@-webkit-keyframes animateSecond {
	0% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform: translateY(80px); }
	14% { transform: translateY(80px); -webkit-transform: translateY(80px); -moz-transform: translateY(80px); }
	15% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform: translateY(0px); }
	29% { transform: translateY(0px); -webkit-transform: translateY(0px); -moz-transform: translateY(0px); }
	30% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform: translateY(-80px); }
	44% { transform: translateY(-80px); -webkit-transform: translateY(-80px); -moz-transform: translateY(-80px); }
	45% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform: translateY(-160px); }
	59% { transform: translateY(-160px); -webkit-transform: translateY(-160px); -moz-transform: translateY(-160px); }
	60% { transform: translateY(-240px); -webkit-transform: translateY(-240px); -moz-transform: translateY(-240px); }
	70% { transform: translateY(-240px); -webkit-transform: translateY(-240px); -moz-transform: translateY(-240px); }
	71% { transform: translateY(-320px); -webkit-transform: translateY(-320px); -moz-transform: translateY(-320px); }
	85% { transform: translateY(-320px); -webkit-transform: translateY(-320px); -moz-transform: translateY(-320px); }
	86% { transform: translateY(-400px); -webkit-transform: translateY(-400px); -moz-transform: translateY(-400px); }
	99% { transform: translateY(-400px); -webkit-transform: translateY(-400px); -moz-transform: translateY(-400px); }
	100% { transform: translateY(-480px); -webkit-transform: translateY(-480px); -moz-transform: translateY(-480px); }
}
