/* with reference to https://codepen.io/danishyma/pen/qBNRQmm */

.gallery-wrapper {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }

.gallery-item {
	width: 100%;
	height: 100%;
	position: relative;
  }
  
  .gallery-item .image {
	width: 100%;
	height: 100%;
	overflow: hidden;
  }
  
  .gallery-item .image img:hover {
	transform: scale(1.1);
  }
  
  .gallery-item .image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
   object-position: 50% 50%;
  cursor: pointer; 
  transition: all 400ms ease-in;
   transform: scale(1); 
   }


  /* Contact Form */
input[type=text], select, textarea {
    width: 100%; /* Full width */
    padding: 12px; /* Some padding */ 
    border: 1px solid #ccc; /* Gray border */
    border-radius: 4px; /* Rounded borders */
    box-sizing: border-box; /* Make sure that padding and width stays in place */
    margin-top: 6px; /* Add a top margin */
    margin-bottom: 16px; /* Bottom margin */
    resize: vertical /* Allow the user to vertically resize the textarea (not horizontally) */
  }
  
  .sumbitBtn {
    background-color: #a73012;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .sumbitBtn:hover {
    background-color: #d10c0c;
  }


  /* Table Page */

  
table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
table td, table th {
    border: 1px solid rgb(34, 33, 33);
    padding: 8px;
  }
  
table tr:nth-child(even){background-color: #110f0f;}
  
table tr:hover {background-color: rgb(46, 43, 43);}
  
table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #350901;
    color: white;
  }


/* Video lightbox refers to https://codepen.io/darcyvoutt/pen/MaamWg/ */

.video-container {
	padding-bottom: 56.25%;
	position: relative;
	padding-top: 30px;
	overflow: hidden;
	height: 0;
  }

.myVideo {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  }

/* Source: https://www.w3schools.com/howto/tryit.asp?filename=tryhow_css_overlay_text */

.lightbox {
	background-color: rgb(0, 0, 0, 0.5);
	bottom: 0;
	display: none;
	height: 100vh;
	left: 0;
	position: fixed;
	right: 0;
	top: 0;
	width: 100vw;
  }

.lightbox-item {
	left: 50%;
	max-height: 75%;
	max-width: 90%;
	position: absolute;
	top: 50%;
	transform: translate(-50%,-50%);
	width: auto;
  }